<template>
  <v-container
    fluid
  >
    <v-row>
      <v-col
        cols="12"
        md="4"
        class="printText"
      >
        <base-material-card
          v-if="rawAddressInfo"
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-account-cash"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('地址信息') }}
            </div>
          </template>
          <v-row>
            <v-col
              cols="12"
              md="12"
            >
              <address-info-block :address-info="rawAddressInfo"/>
            </v-col>
          </v-row>
        </base-material-card>
        <base-material-card
          v-if="rawAddressInfo"
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-printer"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('打印订单') }}
            </div>
          </template>
          <v-row>
            <v-col
              cols="12"
              md="12"
              class="d-flex spaceBetween"
              style="width: 100%"
            >
              <div
                class="mr-2"
              >
                <v-alert
                  border="left"
                  colored-border
                  color="deep-purple accent-4"
                  elevation="2"
                >
                  {{ $t('此处打印的订单仅供参考,不可作为Rechnung使用') }}
                </v-alert>
              </div>

              <v-btn
                right
                large
                color="success"
                dark
                @click.stop="printDialogShow = true"
              >
                {{ $t('打印') }}
              </v-btn>
            </v-col>
          </v-row>
        </base-material-card>
      </v-col>
      <v-col
        id="printText"
        cols="12"
        md="8"
      >
        <base-material-card
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-vuetify"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('菜品信息') }}
            </div>
          </template>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            class="ml-auto"
            hide-details
            :label="$t('Search')"
            single-line
            style="max-width: 250px;"
          />
          <v-divider class="mt-3"/>
          <v-data-table
            :fixed-header="true"
            :headers="headers"
            :items="desserts"
            :items-per-page="15"
            :loading="loading"
            :search.sync="search"
            item-key="id"
            multi-sort
          >
            <template v-slot:item.name="{ item }">
              {{ item.name }}
              <span
                v-if="item.hasMod>0"
                class="red--text font-weight-bold"
              >*</span>
            </template>
            <template v-slot:item.mod="{ item }">
              <div
                :colspan="headers.length"
              >
                <dish-modification :dish-item="item"/>
              </div>
            </template>
          </v-data-table>
          <v-row>
            <v-btn
              class="mb-2"
              color="primary"
              dark
              @click="goback()"
            >
              {{ $t('返回') }}
            </v-btn>
            <v-spacer/>
            <v-btn
              v-print="'#detail'"
              color="green darken-1"
              dark
              @click="reprintOrder"
            >
              {{ $t('补打订单') }}
            </v-btn>
            <v-btn
              v-print="'#printText'"
              class="ml-2 mb-2"
              color="primary"
              dark
            >
              A4 {{ $t('打印') }}
            </v-btn>
          </v-row>
        </base-material-card>
      </v-col>
      <v-dialog
        v-model="printDialogShow"
        max-width="400px"
      >
        <v-card>
          <v-card-title class="headline">
            {{ $t('打印此订单?') }}
          </v-card-title>

          <v-card-text id="detail">
            <v-card-subtitle>{{ $t('地址信息') }}</v-card-subtitle>
            <v-divider/>
            <address-info-block :address-info="rawAddressInfo"/>
            <v-card-subtitle>{{ $t('订单信息') }}</v-card-subtitle>
            <v-divider/>
            <dish-info-block
              :render-list="true"
              :dish-orders="desserts"
            />
          </v-card-text>

          <v-card-actions>
            <v-spacer/>

            <v-btn
              color="green darken-1"
              text
              @click="printDialogShow = false"
            >
              {{ $t('Cancel') }}
            </v-btn>
            <v-btn
              v-print="'#detail'"
              color="green darken-1"
              text
              @click="printDialogShow = false"
            >
              {{ $t('打印') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-col
        cols="12"
      >
        <base-material-card
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-account-cash"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('订单信息') }}
            </div>
          </template>
          <v-data-table
            :fixed-header="false"
            :headers="infoHeaders"
            :items="myinfoList"
            :loading="loading"
            :items-per-page="30"
            hide-default-footer
            hide-default-header
          >
            <template v-slot:item.keyname="{ item }">
              <span class="font-weight-black">
                {{ item.keyname }}
              </span>
            </template>
            <template v-slot:footer>
              <v-toolbar
                color="white"
                flat
              >
                <v-spacer/>
                <v-dialog
                  v-model="dialog"
                  max-width="500px"
                >
                  <template v-slot:activator="{ on }">
                    <v-btn
                      class="mb-2"
                      color="primary"
                      dark
                      v-on="on"
                    >
                      {{ $t('修改支付方式') }}
                    </v-btn>
                  </template>
                  <v-card>
                    <v-card-title>
                      <span class="headline">{{ $t('请选择新的支付方式') }}</span>
                    </v-card-title>
                    <v-card-text>
                      <v-container>
                        <v-row>
                          <template>
                            <v-col
                              cols="12"
                              sm="12"
                            >
                              <v-select
                                v-model="editedItem"
                                :items="payment"
                                item-text="name"
                                item-value="paymentMethodId"
                                :label="$t('chooseyourpayment')"
                                return-object
                              />
                            </v-col>
                          </template>
                        </v-row>
                      </v-container>
                    </v-card-text>

                    <v-card-actions>
                      <v-spacer/>
                      <v-btn
                        color="blue darken-1"
                        text
                        @click="close"
                      >
                        {{ $t('Cancel') }}
                      </v-btn>
                      <v-btn
                        color="blue darken-1"
                        @click="save"
                      >
                        {{ $t('Save') }}
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-toolbar>
            </template>
          </v-data-table>
        </base-material-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { changeMethod, getAllPayment, getDetailOrder, reprintOrder } from '../../components/Utlis/api'
  import GlobalSettings from '../../components/Settings/GlobalSettings'
  import i18n from '../../i18n'
  import Utils from '../../components/Utlis/Utils'
  import AddressInfoBlock from '../../components/base/AddressInfoBlock'
  import DishInfoBlock from '../../components/base/DishInfoBlock'
  import DishModification from '../../components/base/DishModification'
  import Swal from 'sweetalert2'

  export default {
    components: {
      DishModification,
      DishInfoBlock,
      AddressInfoBlock,
    },
    props: {
      id: {
        default: '-1',
        type: String,
      },
    },
    data: function () {
      return {
        loading: false,
        currentIndex: 0,
        orderId: this.id,
        billData: {},
        amountList: [],
        search: '',
        printDialogShow: false,
        expandedItem: [],
        headers: [
          {
            text: this.$i18n.t('code'),
            align: 'start',
            sortable: false,
            value: 'code',
          },
          {
            text: this.$i18n.t('name'),
            value: 'name',
          },
          {
            text: this.$i18n.t('note'),
            value: 'note',
          },
          {
            text: this.$i18n.t('modification'),
            value: 'mod',
          },
          {
            text: this.$i18n.t('createTimestamp'),
            value: 'stamp',
          },
          {
            text: this.$i18n.t('price'),
            value: 'price',
          },
          {
            text: this.$i18n.t('count'),
            value: 'sumCount',
          },
          {
            text: this.$i18n.t('sumPrice'),
            value: 'totalPrice',
          },
        ],
        desserts: [],
        dialog: false,
        PaymentRes: [],
        rawAddressInfo: '',
        infoHeaders: [
          {
            value: 'keyname',
            align: 'left',
          },
          {
            value: 'value',
            align: 'right',
          },
        ],
        myinfoList: [],
        payment: [],
        paymentStatus: '',
        editedItem: {
          paymentMethodId: -1,
          name: '',
        },
        defaultItem: {
          paymentMethodId: -1,
          name: '',
        },
      }
    },
    computed: {
      src: function () {
        if (this.billData.tableInfo) {
          //  console.log(GlobalSettings.SQR + GlobalSettings.genSQUrl + 'TableName=' + this.billData.tableInfo.TableName + 'TableName=' + this.billData.tableInfo.TableName + 'RestaurantId=1' + 'SeatId=1' + 'lang=' + i18n.locale.toUpperCase())
          return `${GlobalSettings.SQR + GlobalSettings.genSQUrl}TableName=` +
            `${this.billData.tableInfo.TableName}&RestaurantId=1&SeatId=1&lang=${i18n.locale.toUpperCase()}`
        } else {
          return false
        }
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
    },
    created () {
      this.reloadItem(this.orderId)
    },
    mounted () {
      // this.loadDetail(this.servantId)
    },
    methods: {
      loadDetail: function (id) {
        getDetailOrder(id).then(res => {
          this.billData = res
          this.desserts = this.billData.billOrderInfo.map((item, index) => ({
            id: index,
            agNameArr: item.hasMod > 0 ? item.agName.split(',') : [],
            aNameArr: item.hasMod > 0 ? item.aName.split(',') : [],
            ...item,
          }))
          // this.editedItem.name = this.billData.billInfo.payMethodName
          this.editedItem.paymentMethodId = this.billData.billInfo.payMethod
          for (const i of this.PaymentRes) {
            if (i.id === this.editedItem.paymentMethodId) {
              this.editedItem.name = i.name
            }
          }
          this.rawAddressInfo = JSON.parse(this.billData.billInfo.rawAddressInfo)
          this.paymentStatus = this.billData.billInfo.paymentStatus === '1' ? this.$i18n.t('已支付') : this.$i18n.t('未支付')
          this.myinfoList =
            [
              {
                keyname: this.$i18n.t('订单号'),
                value: this.billData.billInfo.id,
              },
              {
                keyname: this.$i18n.t('servantName'),
                value: this.billData.billInfo.servantName,
              },
              {
                keyname: this.$i18n.t('tableName'),
                value: this.billData.billInfo.tableName,
              },
              {
                keyname: this.$i18n.t('startTime'),
                value: this.billData.billInfo.startTime,
              },
              {
                keyname: this.$i18n.t('结账时间'),
                value: this.billData.billInfo.endTime,
              },
              {
                keyname: this.$i18n.t('payMethod'),
                value: this.editedItem.name,
              },
              {
                keyname: this.$i18n.t('支付状态'),
                value: this.paymentStatus,
              },
              {
                keyname: this.$i18n.t('personCount'),
                value: this.billData.billInfo.personCount,
              },
              {
                keyname: this.$i18n.t('tipIncome'),
                value: this.billData.billInfo.tipIncome,
              },
              {
                keyname: this.$i18n.t('总税额'),
                value: this.billData.billInfo.tax,
              },
              {
                keyname: this.$i18n.t('totalPrice'),
                value: this.billData.billInfo.totalPrice,
              },
            ]
        })
      },
      reloadItem (id) {
        getAllPayment().then(res => {
          this.PaymentRes = res.content
          this.formatItem()
          this.loadDetail(id)
        })
      },
      async reprintOrder () {
        const type = await Swal.fire({
          title: this.$i18n.t('Bitte wählen Sie eine Rechnungsart.'),
          input: 'select',
          inputOptions: {
            1: this.$i18n.t('Normal Rechnung'),
            2: this.$i18n.t('Rechnung'),
            3: this.$i18n.t('Unternehmensrechnung'),
          },
        })
        let withTitle = 0
        let printCount = 1
        if (type.value > 1) {
          withTitle = 1
        }
        if (type.value > 2) {
          printCount = 2
        }
        reprintOrder(this.id, withTitle, printCount)
      },
      findLangInArray (langArray, target = i18n.locale) {
        return langArray.find((item) => {
          return item.lang.toLowerCase() === target.toLowerCase()
        })
      },
      formatItem () {
        for (const i of this.PaymentRes) {
          i.name = this.findLangInArray(i.langs).name
          this.payment.push({
            paymentMethodId: i.id,
            name: i.name,
          })
        }
      },
      goback () {
        this.$router.history.go(-1)
        // this.$router.push({ name: '桌子管理' })
      },
      save () {
        console.log(this.editedItem.paymentMethodId)
        changeMethod(this.editedItem.paymentMethodId, this.orderId).then(
          () => {
            Utils.toast('编辑成功')
            this.loadDetail(this.orderId)
            this.close()
          },
        )
      },
      close () {
        this.dialog = false
      },
    },
  }
</script>

